// 
import request from '@/js_sdk/cooke-request/request/request'
import Vue from 'vue'
import { apiUrl } from '@/config/api'


// 
request.setConfig({
	withCredentials: true,
	baseUrl: apiUrl, // 
})

// 
request.interceptors.request(config => {
	console.log('req--', config);
	let token = Vue.prototype.$cache.get('token')
	config.header.Authorization = token
	// 

	// 
	// config.header['content-type'] = 'application/json';
	// config.header.token = 'token from interceptors';

	// 
	// config.header = {
	// 'content-type': 'application/json',
	// 'token': 'token from interceptors'
	// }

	// return false; // 
	// return Promise.reject('error from request interceptors'); // catch
	return config; // 
})

// 
request.interceptors.response(res => {
	const { data, config } = res || {}
	const { showError } = config || {}
	const { data: _data, message, error, success } = data || {}
	if (success) {
		return _data
	}
	if (showError) {

		uni.showToast({
			title: error || message,
			duration: 2000
		});
	}
	return Promise.reject(data)

	// 
	// ......

	// return false;    // ,
	// return {message: ''};   // 
	// return Promise.reject('error from response interceptors') // catch
	// return res; // 
})

export default request